@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

hr {
  border: 1px solid rgba(142, 143, 145, 0.507);
}

a {
  font-size: 24px;
  color: white;
  text-decoration: none;
  background: #8fb3e5;
  border: none;
  width: 15%;
  justify-self: center;
  cursor: pointer;
  transition: 1s;
}

a:hover {
  background-color: #454547;
}

.App {
  text-align: center;
  width: 100%;
  height: 100%;
  margin: auto;
  padding: none;
  color: #555557;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.Header {
  background: url(/DeveloperPortfolio/static/media/hero-2.ec674193.jpg) no-repeat center / cover;
  background-color: #706762;
  height: 70vh;
  color: white;
  justify-content: center;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 35% 9% 16% 10% 20%;
  grid-template-areas:
    "."
    "a"
    "b"
    "c"
    ".";
}

.title {
  grid-area: a;
  align-self: center;
  justify-self: center;
  display: grid;
  grid-template-columns: 50% 50%;
  font-family: "Source Sans Pro", sans-serif;
}

.name-title {
  font-size: 62px;
  color: #454547;
  letter-spacing: 8px;
  justify-self: right;
  font-weight: 300;
}
.name-title-last {
  font-size: 62px;
  color: #555557;
  letter-spacing: 9px;
  justify-self: left;
  font-weight: 600;
}

.name-description {
  grid-area: b;
  color: #555557;
  font-size: 28px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 400;
}

.Main-Button {
  grid-area: c;
  font-size: 24px;
  color: white;
  text-decoration: none;
  background: #8fb3e5;
  border: none;
  width: 15%;
  justify-self: center;
  cursor: pointer;
  align-self: center;
  padding-top: 12px;
  padding-bottom: 12px;
  transition: 1s;
}

.Main-Button:hover {
  background: #454547;
}

#Explanation {
  background-color: #eef1f5;
  display: grid;
  padding: 150px;
  font-size: 22px;
}

.Line {
  margin-top: -25px;
  width: 25%;
  justify-self: center;
}

.Paragraph-block {
  justify-self: center;
  width: 40%;
}

#Projects {
  display: grid;
  grid-template-columns: 5% 20% 50% 20% 5%;
  grid-template-rows: 1% 9% 18% 18% 18% 18%;
  grid-template-areas:
    ". . . . ."
    ". exp exp exp ."
    ". . one . ."
    ". . two . ."
    ". . three . ."
    ". . four . .";
  grid-row-gap: 3%;
  row-gap: 3%;
  width: 100%;
  height: 2200px;
  background-color: #262e2b;
  justify-content: center;
}

.Project-Explanation {
  font-size: 22px;
  grid-area: exp;
  color: white;
  justify-self: center;
}

.Project-Line {
  width: 100%;
}

.Project-Text {
  display: flex;
  flex-direction: column;
  justify-self: center;
  align-items: center;
  width: 80%;
}

.Live-Demo {
  padding: 10px;
  margin: 10px;
  width: 50%;
}
.Github-Repo {
  padding: 10px;
  margin: 10px;
  width: 60%;
}

.Project-1-Group {
  grid-area: one;
  display: grid;
  grid-template-columns: 50% 50%;
  background-color: #eef1f5;
}

.Project-1 {
  height: 100%;
  width: 100%;
  background: url(/DeveloperPortfolio/static/media/ArtAPI2.3e6b1dc2.gif) no-repeat center / cover;
  justify-self: center;
  transition: 1s;
}

.Project-1:hover {
  background: url(/DeveloperPortfolio/static/media/ArtAPI.43c13108.gif) no-repeat center / cover;
  justify-self: center;
}

.Project-2-Group {
  grid-area: two;
  display: grid;
  grid-template-columns: 50% 50%;
  background-color: #eef1f5;
}

.Project-2 {
  height: 100%;
  width: 100%;
  background: url(/DeveloperPortfolio/static/media/ShapeSort2.4dbd8075.gif) no-repeat center / cover;
  transition: 1s;
}

.Project-2:hover {
  background: url(/DeveloperPortfolio/static/media/ShapeSort.44b3d728.gif) no-repeat center / cover;
}

.Project-3-Group {
  grid-area: three;
  display: grid;
  grid-template-columns: 50% 50%;
  background-color: #eef1f5;
}

.Project-3 {
  height: 100%;
  width: 100%;
  background: url(/DeveloperPortfolio/static/media/moneystacks2.b9464aa8.gif) no-repeat center/cover;
  transition: 1s;
}

.Project-3:hover {
  background: url(/DeveloperPortfolio/static/media/moneystacks.2869d890.gif) no-repeat center / cover;
}

.Project-4-Group {
  grid-area: four;
  display: grid;
  grid-template-columns: 50% 50%;
  background-color: #eef1f5;
}

.Project-4 {
  height: 100%;
  width: 100%;
  background: url(/DeveloperPortfolio/static/media/Alphabet2.5062e7d2.gif) no-repeat center / cover;
  transition: 1s;
}

.Project-4:hover {
  background: url(/DeveloperPortfolio/static/media/Alphabet.49aa1be2.gif) no-repeat center / cover;
}

.Nav {
  position: fixed;
  display: flex;
  width: 35%;
  justify-content: space-around;
  height: 50px;
  /* background-color: rgba(124, 124, 124, 0.226); */
  left: 63%;
}

.Nav a {
  font-size: 24px;
  color: white;
  text-decoration: none;
  border: none;
  width: 15%;
  justify-self: center;
  cursor: pointer;
  background: none;
  transition: 1s;
}

.Nav a:hover {
  color: #454547;
}

.banner {
  background: url(/DeveloperPortfolio/static/media/banner-2.2b31d648.jpg) no-repeat center / cover;
  height: 230px;
  width: 100%;
}

.quote {
  height: 100%;
  width: 99.9%;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 22px;
  font-weight: 600;
  justify-self: center;
  align-self: center;
  align-content: center;
  color: white;
  border: 1px solid #8fb3e5;
  display: grid;
}

#Contact {
  background-color: #eef1f5;
  justify-self: center;
  padding: 140px;
  display: grid;
  font-size: 22px;
  height: 410px;
}

.form {
  display: grid;
  /* grid-template-columns: 40% 60%; */
}

.form-intro {
  width: 50%;
  justify-self: center;
  align-self: center;
  margin-bottom: 100px;
}

.actual-form {
  width: 90%;
  align-self: center;
  justify-self: center;
  display: grid;
  grid-template-columns: 10% 30% 10% 30% 15%;
}

.Contact-Button {
  font-size: 24px;
  color: white;
  text-decoration: none;
  background: #8fb3e5;
  border: none;
  justify-self: center;
  cursor: pointer;
  align-self: center;
  padding: 12px;
  padding-left: 20px;
  padding-right: 20px;
  transition: 1s;
}

.Contact-Button:hover {
  background: #454547;
}

