.Nav {
  position: fixed;
  display: flex;
  width: 35%;
  justify-content: space-around;
  height: 50px;
  /* background-color: rgba(124, 124, 124, 0.226); */
  left: 63%;
}

.Nav a {
  font-size: 24px;
  color: white;
  text-decoration: none;
  border: none;
  width: 15%;
  justify-self: center;
  cursor: pointer;
  background: none;
  transition: 1s;
}

.Nav a:hover {
  color: #454547;
}
