@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600&display=swap");

.banner {
  background: url("./Assets/banner-2.jpg") no-repeat center / cover;
  height: 230px;
  width: 100%;
}

.quote {
  height: 100%;
  width: 99.9%;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 22px;
  font-weight: 600;
  justify-self: center;
  align-self: center;
  align-content: center;
  color: white;
  border: 1px solid #8fb3e5;
  display: grid;
}

#Contact {
  background-color: #eef1f5;
  justify-self: center;
  padding: 140px;
  display: grid;
  font-size: 22px;
  height: 410px;
}

.form {
  display: grid;
  /* grid-template-columns: 40% 60%; */
}

.form-intro {
  width: 50%;
  justify-self: center;
  align-self: center;
  margin-bottom: 100px;
}

.actual-form {
  width: 90%;
  align-self: center;
  justify-self: center;
  display: grid;
  grid-template-columns: 10% 30% 10% 30% 15%;
}

.Contact-Button {
  font-size: 24px;
  color: white;
  text-decoration: none;
  background: #8fb3e5;
  border: none;
  justify-self: center;
  cursor: pointer;
  align-self: center;
  padding: 12px;
  padding-left: 20px;
  padding-right: 20px;
  transition: 1s;
}

.Contact-Button:hover {
  background: #454547;
}
